export default {
	data() {
		return {
			list: [
				{
					label: "接单",
					value: "warning"
				}
			]
		};
	},

	methods: {
		toDemo({ key }, item) {
			if (item.path) {
				uni.navigateTo({
					url: `/pages/demo/${key}/${item.path}`
				});
			} else {
				this.$refs["toast"].open(`${item.label}开发中`);
			}
		},
		// generateClassName(index) {
		//   // 调用方法，动态生成index
		//   return `fixed-${index}`;
		// }
	}
};
